















































































































	// 评论模块组件
	import commentModule from '@/components/commentModule/commentModule.vue';
	import shoppublic from '@/common/shoppublic.js';
export default {
	onLoad (option) {
		var _this = this;
		// console.log(option);
		_this.getPagesInfo(option.key,option.id);
		_this.getVisitorsList();
		_this.getAboutNewShop();
		_this.getComment();
	},
	components:{
		commentModule:commentModule
	},
	data() {
		return {
			imageUrl:shoppublic.getImageUrl(),
			commentList: '', //评论列表
			pagesInfo:[], //存储当前页面信息
			visitorList:[], //访客列表
			openNecessary:[], //开店必备
		};
	},
	methods: {
		// 获取页面内容
		getPagesInfo(index,id){
			var _this = this;
			// console.log(id);
			uni.request({
				url: shoppublic.getUrl() + 'projectJoin/findProjectJoinByid',
				data:{
					id:id, // 当前页面的id
					token:6239 // 登陆用户的id
				},
				success:res => {
					// console.log(res);
					console.log(res.data.responseBody);
					_this.pagesInfo.push(res.data.responseBody);
					_this.commentList = res.data.responseBody.commentlist;
					// console.log(_this.pagesInfo[0].projectJoin.logo);
				}
			})
		},
		// 图片预览
		asd(index){
			console.log(this.pagesInfo);
			var that = this;
			var current = that.pagesInfo[0].srcs
			wx.previewImage({
				current: current[index], // 当前显示图片的http链接 
				urls: this.pagesInfo[0].srcs // 需要预览的图片http链接列表  
			})
		},
		// visitorsList:访客列表
		getVisitorsList() {
			let _this = this;
			wx.request({
				url: shoppublic.getUrl() + '/projectJoin/accessProject',
				data: {
					// pages_url: 'kaiHaoDian_App/pages/homePageModule/joinInvestment/joinInvestmentDetails/joinInvestmentDetails', //当前页面路径
					pages_url:'kai_index/pages/nav/XMJM/XMJMDetails',//当前页面的路径 /******存在问题: 只能用小程序当前页面的路径,而当前页面的url只能获取一条数据,获取不到其他数据*****/
					page_id: 576, //当前项目id
					access_time: '1', //访问次数
					token: 6239, //访问者id
					pagesize: 0
				},
				success: res => {
					// console.log(res.data.responseBody);
					_this.visitorList = res.data.responseBody;
				}
			})
		},
		//开店必备
		getAboutNewShop() {
			var _this = this;
			uni.request({
				url: shoppublic.getUrl() + '/companySupplier/findListCompanySupplier',
				data: {
				stype: '2',
				sindustry: ''
				},
				success: function(res) {
					// console.log(res.data.responseBody);
					_this.openNecessary = res.data.responseBody;
				}
			});
		},
		//项目评论*****************data数据有问题****************/
		getComment(){
			var _this = this;
			uni.request({
				url: shoppublic.getUrl() + '/comment/addComment',
				data:{
					token:6239,
					id:576,
					wenDaDetailHuiFu:'',
					pktype: 3,
					cid: 0
				},
				success: (res) => {
					console.log(res);
				}
			})
		},
		
		// 跳转访客页面
		toVisitor(){
			uni.navigateTo({
				url:"./mineVisitor"
			})
		},
		// 加载项目信息
		onloadProjectInfo() {
			let _this = this;
			uni.request({
				url:  '',
				data: {},
				success: res => {
					console.info(res);
				}
			});
		},
		// 跳转名片
		toCard() {
			uni.navigateTo({
				url: '' //需携带用户id
			});
		},
		toCaller() {
			uni.navigateTo({
				url: '' //需携带项目id
			});
		},
		shareFriends() {
			uni.share({
				provider: 'weixin',
				scene: 'WXSenceTimeline',
				type: 0,
				href: 'http://uniapp.dcloud.io/',
				title: 'uni-app分享',
				summary: '我正在使用开好店APP寻找加盟供应商~ 一起来使用吧',
				imageUrl: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png',
				success: function(res) {
					console.log('success:' + JSON.stringify(res));
				},
				fail: function(err) {
					console.log('fail:' + JSON.stringify(err));
				}
			});
		}
	},
	// 原生按钮点击事件
	onNavigationBarButtonTap(e) {
		console.info(JSON.stringify(e));
		let _this = this;
		if (e.index === 0) {
			_this.shareFriends();
		}
	}
};
